import React from "react";
import "./Event.css";
function Event({ time, player, event, side }) {
  return (
    <div className="event">
      <span className="event-time">{time}'</span>
      {event === "GOAL" ? (
        <img src="/image/football-ball.png" alt="..." />
      ) : event === "YELLOW_CARD" ? (
        <img src="/image/yellow-card.png" alt="..." />
      ) : event === "RED_CARD" ? (
        <img src="/image/red-card.png" alt="..." />
      ) : event === "YELLOW_RED_CARD" ? (
        <img src="/image/red-card.png" alt="..." />)
        :
        event === "GOAL_PENALTY" ? 
        <img src="/image/goal.png" alt="..."/>
        : 
        event === "OWN_GOAL" ?
           <>
        <img src="/image/football-ball.png" alt="..."/>
        <span>[OG]</span>
        </>
        :
        <img src="/image/change.png" alt="..."/>
      }
      <span className="player-name">
        {player} [{side === "h" ? (event === "OWN_GOAL" ? "away" : "home") : "away"}]
      </span>
    </div>
  );
}

export default Event;
